*, h1, h2, p {
  margin: 0;
  padding: 0;
}

body {
  padding-left: 40px;
  padding-right: 40px;
  overflow-x: hidden;
}

.select-container {
  margin-top: 20px;
}

.breed-select {
  width: 150px;
  margin-bottom: 10px;
  padding: 4px;
}

.box {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.cat-info {
  gap: 30px;
  display: flex;
}

.text-container {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.title, .text, .subtitle {
  color: #22343d;
  font-family: sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
}

.text {
  font-size: 16px;
}

.subtitle {
  font-size: 22px;
  font-weight: 600;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-bottom-style: dotted;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack 1s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.error {
  font-weight: 16px;
  color: red;
  font-weight: 500;
}

.is-hidden {
  display: none;
}

